<template>
  <div class="timeline">
    <el-container>
      <el-aside width="200px">
        <el-menu :default-active="view_year" @select="handelSelect">
          <el-menu-item v-for="year in $timeline_years" :key="year" :index="year">
            <span slot="title" v-text="year"></span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-main>
        <el-timeline>
          <el-timeline-item
            v-for="(item, index) in timelines"
            :key="index"
            :timestamp="item.timestamp"
            placement="top"
            :type="item.type || 'success'"
          >
            <el-card>
              <h4 v-text="item.title"></h4>
              <p v-if="item.caption" v-html="item.caption"></p>
              <el-collapse v-if="item.list" accordion>
                <el-collapse-item  v-for="(li, index) in item.list" :key="index" :title="'- ' + li.title" :name="index">
                  <div v-html="li.detail" style="color: #303133; font-size: 13px;"></div>
                </el-collapse-item>
              </el-collapse>
            </el-card>
          </el-timeline-item>
        </el-timeline>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "Timeline",

  data() {
    return {
      view_year: "2017"
    };
  },

  computed: {
    timelines() {
      return this.$timeline[this.view_year];
    }
  },

  methods: {
    handelSelect(year) {
      this.view_year = year;
    }
  }
};
</script>

<style>
</style>


